.gpt3__header {
  display: flex;
}

.gpt3__header-content {
  display: flex;
  flex-direction: column;
  flex: 1;
  justify-content: center;
  align-items: flex-start;

  margin-right: 5rem;
}

.gpt3__header-content h1 {
  font-family: var(--font-family);
  font-weight: 800;
  font-size: 62px;
  line-height: 75px;
  letter-spacing: -0.04em;
}

.gpt3__header-content p {
  font-family: var(--font-family);
  font-style: normal;
  font-weight: normal;
  font-size: 20px;
  line-height: 27px;
  color: var(--color-text);

  margin-top: 1.5rem;
}

.gpt3__header-content__input {
  width: 100%;
  margin: 2rem 0 1rem;

  display: flex;
}

.gpt3__header-content__input input {
  /* This will give it more width */
  flex: 2;
  width: 100%;
  min-height: 50px;

  /* background: var(--color-footer); */
  background: #052d56;
  /* border-radius: 5px 0px 0px 5px; */
  border-top-left-radius: 5px;
  border-bottom-left-radius: 5px;
  border: 2px solid #052d56;
  padding: 0 1rem;
  outline: none;
  /* Color of input text */
  color: #fff;

  font-family: var(--font-family);
  font-size: 20px;
  line-height: 27px;
  /* font-style: normal;
  font-weight: normal; */
}

.gpt3__header-content__input button {
  /* Make the button take more space */
  flex: 0.6;
  width: 100%;
  min-height: 50px;
  padding: 0 1rem;

  background: #ff4820;
  /* border-radius: 5px 0px 0px 5px; */
  border-top-right-radius: 5px;
  border-bottom-right-radius: 5px;
  outline: none;
  border: none;

  font-family: var(--font-family);
  font-weight: bold;
  font-size: 20px;
  line-height: 27px;
  color: #ffffff;

  cursor: pointer;
}

.gpt3__header-content__people {
  display: flex;
  width: 100%;
  justify-content: flex-start;
  /* Makes the images have normal height */
  align-items: center;

  margin-top: 2rem;
}

.gpt3__header-content__people img {
  width: 181.79px;
  height: 38px;
}

.gpt3__header-content__people p {
  margin: 0 0 0 1rem;
  text-align: center;

  font-family: var(--font-family);
  font-weight: 500;
  font-size: 12px;
  line-height: 38px;
  color: #ffffff;
}

.gpt3__header-image {
  display: flex;
  flex: 1;
  justify-content: center;
  align-items: center;
}

.gpt3__header-image img {
  width: 100%;
  /* height: 100%; */
  height: auto;
}

@media screen and (max-width: 1050px) {
  .gpt3__header {
    flex-direction: column;
  }

  .gpt3__header-content {
    margin: 0 0 3rem;
  }
}

@media screen and (max-width: 650px) {
  .gpt3__header h1 {
    font-size: 48px;
    line-height: 60px;
  }

  .gpt3__header p {
    font-size: 16px;
    line-height: 24px;
  }

  .gpt3__header-content__people {
    flex-direction: column;
  }

  .gpt3__header-content__people p {
    margin: 0;
  }

  .gpt3__header-content__input input,
  .gpt3__header-content__input button {
    font-size: 16px;
    line-height: 24px;
  }
}

@media screen and (max-width: 490px) {
  .gpt3__header h1 {
    font-size: 36px;
    line-height: 48px;
  }

  .gpt3__header p {
    font-size: 14px;
    line-height: 24px;
  }

  .gpt3__header-content__input input,
  .gpt3__header-content__input button {
    font-size: 14px;
    line-height: 16px;
  }
}
