.gpt3__features {
  display: flex;
  flex-direction: row;
  width: 100%;

  justify-content: space-evenly;
  align-items: flex-start;
}

.gpt3__features-heading {
  display: flex;
  flex-direction: column;
  flex: 1;

  justify-content: flex-start;
  align-items: flex-start;

  text-align: left;
  margin-right: 5rem;
}

.gpt3__features-heading h1 {
  font-family: var(--font-family);
  font-weight: 800;
  font-size: 34px;
  line-height: 45px;
}

.gpt3__features-heading p {
  font-family: var(--font-family);
  font-weight: 500;
  font-size: 16px;
  line-height: 30px;

  color: #ff8a71;

  margin-top: 2rem;
}

.gpt3__features-container {
  display: flex;
  flex-direction: column;
  flex: 1.5;

  justify-content: flex-start;
  align-items: center;
}

@media screen and (max-width: 990px) {
  .gpt3__features {
    flex-direction: column;
  }

  .gpt3__features-heading {
    margin: 0 0 2rem 0;
  }
}

@media screen and (max-width: 550px) {
  .gp3__features-heading h1 {
    font-size: 28px;
    line-height: 38px;
  }
}
